<template>
  <div class="p-4">
    <h1 class="text-2xl font-bold mb-6">üéì Diplom ma'lumotlari</h1>

    <VaCard>
      <VaCardContent>
        <template v-if="isDiplom">
          <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
            <VaInput v-model="form.serialNumber" label="Diplom seriyasi va raqami" :readonly="true" />
            <VaInput v-model="form.educationType" label="Ta'lim darajasi" :readonly="true" />
            <VaInput v-model="form.institutionType" label="Muassasa turi" :readonly="true" />
            <VaInput v-model="form.universityName" label="OTM nomi" :readonly="true" />
            <VaInput v-model="form.graduationYear" label="Bitirgan yil" :readonly="true" />
            <VaInput v-model="form.region" label="Viloyat (ID)" :readonly="true" />
            <VaInput v-model="form.district" label="Tuman (ID)" :readonly="true" />
          </div>
        </template>

        <template v-else-if="isTransferDiplom">
          <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
            <VaInput v-model="form.transferCountry" label="Mamlakat (ID)" :readonly="true" />
            <VaInput v-model="form.transferUniversity" label="Universitet nomi" :readonly="true" />
            <VaInput v-model="form.transferTargetCourse" label="Ko‚Äòchirilayotgan yo‚Äònalish (ID)" :readonly="true" />
          </div>
        </template>

        <div class="mt-8">
          <VaAlert v-if="!isDiplom && !isTransferDiplom" color="info">
            ‚ö†Ô∏è Diplom ma ºlumotlari topilmadi yoki foydalanuvchi tizimga kirmagan.
          </VaAlert>
        </div>
      </VaCardContent>
    </VaCard>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, computed } from 'vue'
import { useAuthStore } from '@/stores/auth-store'

const auth = useAuthStore()

const form = ref({
  serialNumber: '',
  educationType: '',
  institutionType: '',
  universityName: '',
  graduationYear: '',
  region: '',
  district: '',

  transferCountry: '',
  transferUniversity: '',
  transferTargetCourse: '',
})

const isDiplom = computed(() => !!auth.user?.diplom)
const isTransferDiplom = computed(() => !!auth.user?.transfer_diplom)

onMounted(() => {
  const d = auth.user?.diplom
  const t = auth.user?.transfer_diplom

  if (d) {
    form.value.serialNumber = d.serial_number || ''
    form.value.educationType = d.education_type || ''
    form.value.institutionType = d.institution_type || ''
    form.value.universityName = d.university_name || ''
    form.value.graduationYear = d.graduation_year || ''
    form.value.region = d.region?.toString() || ''
    form.value.district = d.district?.toString() || ''
  } else if (t) {
    form.value.transferCountry = t.country?.toString() || ''
    form.value.transferUniversity = t.university_name || ''
    form.value.transferTargetCourse = t.target_course?.toString() || ''
  }
})
</script>
